@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-fb:before { content: ""; }

.icon-gp:before { content: ""; }

.icon-play:before { content: ""; }

.icon-tw:before { content: ""; }

@font-face { font-family: Roboto-Light; src: url("fonts/Roboto-Light.woff2") format("woff2"), url("fonts/Roboto-Light.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: Roboto-Regular; src: url("fonts/Roboto-Regular.woff2") format("woff2"), url("fonts/Roboto-Regular.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: Roboto-Bold; src: url("fonts/Roboto-Bold.woff2") format("woff2"), url("fonts/Roboto-Bold.woff") format("woff"); font-weight: normal; font-style: normal; }
body { font-size: 15px; font-family: "Roboto-Regular", Arial, sans-serif; line-height: 1.15385; }

.container { max-width: 1354px; margin: 0 auto; padding: 0 20px; *zoom: 1; }
.container:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 767px) { .container { padding: 0 10px; } }

img { display: block; max-width: 100%; width: 100%; }

.wrapper { padding-bottom: 135px; position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; background-image: url(../img/bg/bg-top.png), url(../img/bg/bg-bot.png); background-repeat: no-repeat; background-position: top right, bottom 75px left; background-color: #101a25; }
@media only screen and (max-width: 1299px) { .wrapper { background-position: top right -470px, bottom 75px left; } }
@media only screen and (max-width: 1023px) { .wrapper { padding-bottom: 77px; background-position: top right -550px, bottom -50px left; } }
@media only screen and (max-width: 769px) { .wrapper { padding-bottom: 0; } }
@media only screen and (max-width: 755px) { .wrapper { background-position: top 0px right -345px, bottom left; background-size: 600px 110px, 0 0; } }

.header { padding: 25px 0 43px; }
@media only screen and (max-width: 1299px) { .header { padding-bottom: 30px; } }
@media only screen and (max-width: 755px) { .header { padding: 10px 0; } }
.header__container { display: flex; justify-content: space-between; align-items: flex-end; max-width: 1354px; margin: 0 auto; padding: 0 20px; }
@media only screen and (max-width: 1299px) { .header__container { flex-direction: column; align-items: flex-start; } }
@media only screen and (max-width: 1023px) { .header__container { flex-direction: row; } }
@media only screen and (max-width: 755px) { .header__container { flex-direction: column; padding: 0 10px; } }

.logo { display: block; margin-left: -7px; width: 176px; height: 62px; }
@media only screen and (max-width: 1299px) { .logo { margin-bottom: 20px; } }
@media only screen and (max-width: 755px) { .logo { float: none; margin: 0 auto 20px; } }

.balance { width: 86.60578%; display: flex; justify-content: flex-end; }
@media only screen and (max-width: 1299px) { .balance { width: 100%; justify-content: space-between; } }
@media only screen and (max-width: 1023px) { .balance { flex-direction: column; width: 72%; margin-left: auto; } }
@media only screen and (max-width: 755px) { .balance { width: 100%; } }
.balance__col { display: flex; align-items: center; }
@media only screen and (max-width: 1299px) { .balance__col { align-items: center; flex-basis: 49%; } }
@media only screen and (max-width: 1023px) { .balance__col { justify-content: flex-start; } }
@media only screen and (max-width: 755px) { .balance__col { justify-content: space-between; } }
@media only screen and (max-width: 470px) { .balance__col + .balance__col { margin-top: 5px; } }
@media only screen and (max-width: 1023px) { .balance__col:nth-of-type(1) :nth-child(3) { margin-right: 12px; } }
.balance__col--first { position: relative; margin-right: 52px; }
@media only screen and (max-width: 1299px) { .balance__col--first { margin-right: 0; } }
.balance__col--first::before { content: ""; position: absolute; top: -45px; right: -28px; width: 1px; height: 85px; background-color: #2f3842; }
@media only screen and (max-width: 1299px) { .balance__col--first::before { height: 35px; top: 3px; right: -12px; } }
@media only screen and (max-width: 1023px) { .balance__col--first::before { display: none; content: none; } }
.balance__label { margin-right: 20px; font-size: 13px; color: rgba(255, 255, 255, 0.4); }
@media only screen and (max-width: 1299px) { .balance__label { margin-right: 7px; } }
.balance__label--mobile { margin-right: 13px; }
@media only screen and (max-width: 755px) { .balance__label--mobile { display: none; } }
@media only screen and (max-width: 1023px) { .balance__label--first { margin-right: 16px; } }
.balance__bill { margin-right: 20px; font-size: 40px; line-height: 1; font-family: "Roboto-Light", Arial, sans-serif; color: #e81d25; }
@media only screen and (max-width: 1299px) { .balance__bill { margin-right: 7px; } }
@media only screen and (max-width: 1023px) { .balance__bill { margin-right: 21px; } }
@media only screen and (max-width: 470px) { .balance__bill { margin-right: 6px; font-size: 30px; } }
.balance__bill--second { color: #aa7e3a; }
@media only screen and (max-width: 1023px) { .balance__bill--second { margin-right: 46px; } }
@media only screen and (max-width: 755px) { .balance__bill--second { margin-right: 46px; } }
@media only screen and (max-width: 470px) { .balance__bill--second { margin-right: 25px; } }
.balance__form { display: flex; }
@media only screen and (max-width: 1299px) { .balance__form { flex-grow: 1; } }
@media only screen and (max-width: 1023px) { .balance__form { justify-content: flex-end; } }
@media only screen and (max-width: 470px) { .balance__form { justify-content: flex-start; margin-left: 14px; } }
.balance__input { width: 140px; color: #fff; font-size: 20px; height: 35px; text-align: center; border: 1px solid #28313b; border-right: none; background-color: #101a25; -webkit-appearance: none; border-radius: 0; font-family: "Roboto-Light", Arial, sans-serif; }
.balance__input::placeholder { color: #fff; }
@media only screen and (max-width: 1299px) { .balance__input { flex-basis: 55%; } }
@media only screen and (max-width: 470px) { .balance__input { width: 75px; height: 30px; } }
.balance__btn { padding: 0 5px; width: 110px; height: 35px; font-size: 13px; text-transform: uppercase; color: #fff; background-color: #e81d25; font-family: "Roboto-Bold", Arial, sans-serif; transition: background-color 0.2s ease-in; }
.balance__btn:hover { background-color: #a71117; }
.balance__btn--color { background-color: #aa7e3a; }
.balance__btn--color:hover { background-color: #715427; }
@media only screen and (min-width: 470px) { .balance__btn br { display: none; } }
@media only screen and (max-width: 1299px) { .balance__btn { flex-basis: 45%; } }
@media only screen and (max-width: 470px) { .balance__btn { width: 70px; font-size: 12px; height: 30px; } }

.games__inner { pointer-events: none; position: relative; margin-left: -6px; margin-right: -6px; *zoom: 1; }
.games__inner:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 755px) { .games__inner { margin-left: -5px; margin-right: -5px; } }
.games__inner:hover .games__item::before { opacity: 1; }
.games__inner .games__item:hover::before { opacity: 0; }
.games__col { position: relative; float: left; width: calc(33.333333% - 12px); margin: 6px; z-index: 1; }
@media only screen and (max-width: 1023px) { .games__col { width: calc(50% - 12px); } }
@media only screen and (max-width: 755px) { .games__col { margin: 5px; width: calc(50% - 10px); } }
.games__item { pointer-events: auto; position: relative; display: block; text-decoration: none; transition: all 0.3s ease-in-out; z-index: 1; }
.games__item::before { opacity: 0; content: ""; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background-color: rgba(16, 26, 37, 0.7); z-index: 1; transition: opacity 0.3s ease-in-out; }
.games__item:hover { box-shadow: 0 0 87px 15px rgba(232, 29, 37, 0.3); z-index: 5; }
.games__item:hover .games__btn { background-color: #e81d25; }
.games__item:hover .games__icon { color: #fff; }
.games__inner .games__item:hover::before { opacity: 0; }
.games__btn { position: relative; padding: 15px 0; font-size: 14px; color: #fff; letter-spacing: 0.05em; text-transform: uppercase; text-align: center; text-indent: 6px; background-color: #1c2630; font-family: "Roboto-Bold", Arial, sans-serif; transition: background-color 0.3s ease-in-out; }
@media only screen and (max-width: 755px) { .games__btn { padding: 8px 0 6px; font-size: 12px; } }
.games__icon { display: inline-block; vertical-align: middle; position: relative; right: 20px; font-size: 30px; transition: color 0.3s ease-in-out; color: #e21d25; }
@media only screen and (max-width: 755px) { .games__icon { font-size: 16px; top: -2px; right: 10px; } }

.footer { position: relative; margin-top: -135px; padding: 57px 0; text-align: center; }
@media only screen and (max-width: 1023px) { .footer { margin-top: -77px; padding: 28px 0; } }
@media only screen and (max-width: 769px) { .footer { display: none; } }
.footer__col { width: 33.33333%; float: left; *zoom: 1; }
.footer__col:after { content: " "; display: table; clear: both; }
.footer__col:nth-child(1) { width: 20%; }
.footer__col:nth-child(2) { width: 60%; }
.footer__col:nth-child(3) { width: 20%; }
.footer__copy { float: left; color: rgba(255, 255, 255, 0.4); }
.footer__nav { font-size: 0; }
.footer__nav a { display: inline-block; margin: 0 18px; text-decoration: none; font-size: 15px; color: #fff; transition: 0.2s ease-in; }
.footer__nav a:hover { color: rgba(255, 255, 255, 0.4); }
.footer__social { float: right; }

.social a { float: left; font-size: 17px; color: #585f67; text-decoration: none; transition: color 0.2s ease-in; }
.social a + a { margin-left: 27px; }
.social a:hover { color: #fff; }


